<template>
  <div class='main'>
    <div class='contain df'>
      <div class='contain-aside' :class="[isSlide ? 'contain-aside-hide' : '']">
        <div class='slide-main'>
          <Tree @callback="treeCallback" :reload="reloadTree" @finishReload="finishReload" :filter="true"></Tree>
        </div>
        <div class='slide-bar'>
          <span class='slide-icon' @click='handleSlideToggle'></span>
        </div>
      </div>
      <div class='contain-main flex1'>
        <div class='page-main'>
          <div class='search'>
            <el-form :inline='true' class='demo-form-inline' size='small' ref="searchParams" :model="searchParams" >
              <el-form-item>
                <el-input
                  placeholder='姓名/工号/帐号/手机'
                  v-model='searchParams.search'>
                  <i slot='prefix' class='el-input__icon el-icon-search'></i>
                </el-input>
              </el-form-item>
              <el-form-item>
                <el-select v-model="searchParams.role_id">
                  <el-option label='全部职务' value='' key="999"></el-option>
                  <el-option v-for="(label, index) in roleDict" :label=label :value=index :key="index"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-select v-model="searchParams.status">
                  <el-option label='全部状态' value=''></el-option>
                  <el-option label='启用帐号' value='1'></el-option>
                  <el-option label='禁用帐号' value='0'></el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type='primary' @click="requestData">查询</el-button>
              </el-form-item>
            </el-form>
          </div>
          <div class='action-bar'>
            <el-button type='primary' size='small' @click="addMemberShow">新增人员</el-button>
            <el-button type='default' size='small'>导入人员</el-button>
            <el-dropdown style='margin-left: 10px' @command="handleCommand">
              <el-button type="default" size='small'>
                更多操作
                <i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="export" :disabled="!this.tableSelectionItems.length">批量导出</el-dropdown-item>
                <el-dropdown-item command="stopUser" :disabled="!this.tableSelectionItems.length" >停用账号</el-dropdown-item>
                <el-dropdown-item command="resetPwd" :disabled="!this.tableSelectionItems.length">密码重置</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
          <el-table
            :data='tableDataList'
            stripe
            border
            type='selection'
            style='width: 100%'
            @select="onSelectionChange"
            @select-all="onSelectionChange"
          >
            <el-table-column
              type='selection'
              fixed='left'
              width='40'>
            </el-table-column>
            <el-table-column
              v-for='item in columns'
              :prop='item.key'
              :label='item.title'
              :formatter='item.formatter'
              :key='item.key'
              :width='item.width'>
            </el-table-column>
            <el-table-column
              fixed='right'
              prop='zip'
              label='操作'
              width='220'>
              <template slot-scope='scope'>
                <el-button class='no-decoration' type="text"
                   @click='handleClick(scope.row)'>编辑</el-button>
                <el-button class='no-decoration' type="text"
                           @click='handleChangeStatus(scope.row)'>{{statusFormatter(scope.row, {method: true})}}</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class='page-wrapper'>
            <el-pagination
              @size-change='handleSizeChange'
              @current-change='handleCurrentChange'
              :current-page='tableParams.page_num'
              :page-sizes='tableOption.pageSizeList'
              :page-size='tableParams.page_size'
              :layout='tableOption.layout'
              :total='tableTotalCount'>
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
    <AddDialog @closeDialog="closeDialog" :dialogFormVisible="addMember"></AddDialog>
    <EditDialog @closeDialog="closeDialog" :dialogFormVisible="editMember" :formDataId="editMemberId"></EditDialog>
  </div>
</template>

<script>
  import tableMixin from '@/mixins/tableMixin'
  import AddDialog from './AddDialog.vue'
  import EditDialog from './EditDialog.vue'
  import { mapActions, mapGetters } from 'vuex'
  import Tree from '../common/Tree.vue'

  export default {
    components: {
      AddDialog, Tree, EditDialog
    },
    data() {
      return {
        editMemberId: '',
        searchParams: {search: '', status: '', role_id: ''},
        extraParams: {id: ''},
        roleDict: ['部门主管', '维护人员', '组长', '巡检人员'],
        treeBackData: {},
        reloadTree: {},
        addMember: false,
        editMember: false,
        currentPage: 1,
        isSlide: false,
        columns: [
          {title: '姓名', key: 'staff_name'},
          {title: '工号/帐号', key: 'job_no'},
          {title: '部门', key: 'department_name'},
          {title: '职位', key: 'role_name'},
          {title: '手机号', key: 'phone'},
          {title: '微信', key: 'wechat_no'},
          {title: '状态', key: 'status', formatter: this.statusFormatter},
        ],
        defaultProps: {
          children: 'childList',
          label: 'departmentName'
        }
      }
    },
    mixins: [tableMixin],
    computed: {
      employees() {
        if (this.employeesList()) {
          return this.employeesList()
        }
        return false
      }
    },
    methods: {
      ...mapActions(['getEmployeesList', 'postEmployees', 'changeEmployeesStatus', 'closeEmployees', 'resetEmployeesPwd']),
      ...mapGetters(['employeesList']),
      initTableServer() {
        return Promise.resolve(
          this.tableServer = {
            loadTableData: this.getEmployeesList,
            dataList: this.employeesList,
            extraParams: this.extraParams,
            searchParams: this.searchParams
          }
        )
      },
      statusFormatter(value, option) {
        if (Number(value.status) === 0) {
          if (option.method) {
            return '启用'
          }
          return '禁用'
        } else if (Number(value.status) === 1) {
          if (option.method) {
            return '禁用'
          }
          return '启用'
        }
      },
      treeCallback(obj) {
        console.log('treeCallback')
        console.log(obj)
        this.treeBackData = obj
        if (this.treeBackData.id) {
          this.extraParams.id = this.treeBackData.id
          this.requestData && this.requestData()
        }
      },
      finishReload() {
        this.reloadTree = false
      },
      closeDialog(option) {
        if (option && option.reload) {
          this.requestData && this.requestData()
        }
        if (option && option.close) {
          this.addMember = false
          this.editMember = false
        }
        if (!option) {
          this.addMember = false
          this.editMember = false
        }
      },
      addMemberShow() {
        this.addMember = true
      },
      handleClick(row) {
        this.editMemberId = row.id
        this.editMember = true
      },
      handleSlideToggle() {
        this.isSlide = !this.isSlide
      },
      handleChangeStatus(row) {
        this.changeEmployeesStatus({id: row.id, status: Number(!Number(row.status))}).then(() => {
          this.requestData && this.requestData()
        })
      },
      handleCommand(command) {
        switch (command) {
          case 'stopUser':
            this.handleCloseUsers()
            break
          case 'resetPwd':
            this.handleResetPwd()
            break
        }
      },
      handleResetPwd() {
        if (this.tableSelectionItems.length) {
          const result = this.tableSelectionItems.map((list) => list.id)
          this.resetEmployeesPwd(result)
        }
      },
      handleCloseUsers() {
        if (this.tableSelectionItems.length) {
          const result = this.tableSelectionItems.map((list) => list.id)
          this.closeEmployees(result).then(() => {
            this.requestData && this.requestData()
          })
        }
      }
    },
  }
</script>
<style lang='scss' src='./style.scss'>
</style>


